@import "~bootstrap/dist/css/bootstrap.min.css";
@import "~react-tagsinput/react-tagsinput.css";

html,
body,
div#root,
div#cfg_course_gen_ui {
  height: 100%;
  width: 100%;
}

body {
  font-family: "Lato", sans-serif;
}

div#cfg_course_gen_ui {
  display: flex;
  flex-direction: column;
}

.page {
  flex: 1;
  overflow-y: auto;
  padding: 1rem 2rem;
}

.h-100 {
  height: 100%;
}

.w-100 {
  width: 100%;
}

.pointer {
  cursor: pointer;
}

// Ellipsification CSS
.ellipsis {
  display: -webkit-box;
  white-space: pre-wrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  line-height: 25px;
}

.clamp-1 {
  -webkit-line-clamp: 1;
}

.clamp-2 {
  -webkit-line-clamp: 2;
}

.clamp-3 {
  -webkit-line-clamp: 3;
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background-color: #004767;
  font-weight: bold;
}

.nav-link,
.nav-link:hover {
  color: #333;
  font-size: 13px;
}

// Switch CSS

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 25px;

  input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 17px;
    width: 17px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: #2196f3;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(14px);
    -ms-transform: translateX(14px);
    transform: translateX(14px);
  }

  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }
}

// Custom Scroll Bar CSS
/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #cccccc;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
