.bit-card {
  border: none !important;
  border-radius: 0px !important;

  .card-body {
    padding: 0.5rem;

    .bit-wrapper {
      display: flex;
      flex-direction: row;
      align-items: center;

      .bit-image {
        width: 3rem;
        height: 3rem;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .bit-info {
        flex: 1;

        .bit-overview {
          line-height: 1.4rem;
        }
      }

      .action-panel {
        display: flex;
        justify-content: space-evenly;

        .action-button {
          width: 2.5rem;
          height: 2.5rem;
          background: #ebebeb;
          border-radius: 13rem;
          display: flex;
          align-items: center;
          justify-content: center;
          margin: 0 5px;
        }
      }
    }
  }
}
